body {
  height: 100vh;
  background-color: #c3c3c3;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calculator {
  background-color: #fff;
  border-radius: 10px;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 20px;
  display: grid;
  box-shadow: 0 4px 6px #0000001a;
}

.display {
  height: 50px;
  background-color: #eee;
  border-radius: 5px;
  grid-column: span 4;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
  padding: 0 10px;
  font-family: Arial, sans-serif;
  font-size: 24px;
  display: flex;
}

button {
  height: 50px;
  color: #333;
  cursor: pointer;
  background-color: #dedede;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  font-weight: bold;
}

button:hover {
  background-color: #c3c3c3;
}

img {
  max-width: 250px;
}

